.vehicle-filters {
  .field-set {
    max-height: 1000px;
    height: 105px;
    overflow: hidden;
    transition: max-height 0.5s var(--transition-effect);
  }

  &.collapsed .field-set {
    max-height: 0;
    transition: max-height 0.5s var(--transition-effect);
  }
}

.collapse-btn {
  background-color: transparent;
  border: none;
}

.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
  text-align: start;
  font-weight: 600;
  line-height: 1.2;
  color: var(--primart-text-color);
  margin-bottom: 24px;
  padding-bottom: 24px;
  border-bottom: 1px solid rgba(16, 24, 40, 0.1254901961);
}

.field-set {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  border: none;
  padding: 0;
}

.input-wrap {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: calc((100% - 2 * 10px) / 3);
  height: 85px;

  @media screen and (min-width: 768px) {
    height: 95px;
  }

  input {
    width: 100%;
    height: 100%;
    padding: 15px;
    position: absolute;
    opacity: 0;
    cursor: pointer;
    pointer-events: all;

    &:checked + div {
      border-color: var(--accent-color);
    }
  }
}

.radio-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 5px;
  text-align: center;
  font-size: 12px;
  font-weight: 500;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  border: 1px solid #10182820;
  padding: 10px;
  color: var(--primary-text-color);
  cursor: pointer;
  pointer-events: all;
  margin-top: 20px;

  @media screen and (min-width: 768px) {
    font-size: 16px;
  }

  @media screen and (min-width: 1440px) {
    padding: 17px;
    font-size: 14px;
  }

  svg {
    stroke: var(--secondary-text-color);
    padding-bottom: 8px;
    width: 44px;
    height: 30px;
  }
}
